import { Component,Renderer, ElementRef, OnDestroy } from '@angular/core';
import {ViewChild} from "@angular/core";
import {AfterViewInit} from "@angular/core";
//import { request } from 'https';
@Component({
  selector: `app-root`,
  template: `
    <div>
      <canvas #myCanvas width="400" height="400" style="background:while;" 
      (touchmove) ="tick($event)" (touchstart)="engage($event)" (touchend)="disengage()"></canvas>
      
    </div>
    `
})
export class AppComponent implements AfterViewInit {
  rectW:number = 100;
  rectH:number = 100;
  rectColor:string = "#ffffff";
  context:CanvasRenderingContext2D;
  dragging = false;
  @ViewChild("myCanvas") myCanvas;

  ngAfterViewInit() {

    let canvas = this.myCanvas.nativeElement;
    this.context = canvas.getContext("2d");
   // this.tick();
    
  }

  tick = function(event) {
    console.log("event",event);
    var ctx = this.context;
    ctx.lineWidth = 2.8;
  if(this.dragging){
    
    // ctx.clearRect(0, 0, 400, 400);
    // ctx.fillStyle = this.rectColor;
    // ctx.fillRect(0, 0, this.rectW, this.rectH);
    ctx.lineTo(event.touches[0].clientX, event.touches[0].clientY);
    ctx.stroke();
    ctx.beginPath();
    //ctx.fillRect(event.offsetX, event.offsetY, 10,  Math.PI*2);
    ctx.fillRect(event.touches[0].clientX, event.touches[0].clientY, 1, 1);
    //ctx.arc(event.touches[0].clientX, event.touches[0].clientY, 10, 0, Math.PI*2);
    ctx.fillStyle = "black";
    ctx.beginPath();
    ctx.moveTo(event.touches[0].clientX, event.touches[0].clientY);
    //console.log ("tick",this.context);
    }
    

  }

  engage =function(event){
    // requestAnimationFrame(()=>{
    //   this.engage(event);
    // });
    console.log("event",event.touches[0].clientX);
    console.log ("engage");
    this.dragging = true;
    this.tick(event);
    
  }

  disengage = function(){
    var ctx = this.context;
    this.dragging = false;
    ctx.beginPath();
    
  }
  
}




